body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.layouts__wrapper {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.layouts__wrapper {
  display: flex;
}

.MyCounter {
  position: center;
  text-align: center;
  font-size: 3rem;
}
button {
  width: auto;
  font-size: 1rem;
  display: flex;
  border-radius: 5%;
  border-color: aqua;
  margin: 1%;
  padding: 1%;
  background-color: gray;
  opacity: 70%;
}
button:hover {
  font-size: 1rem;
  background-color: gainsboro;
  opacity: 100%;
}

